$primary: #a5d8ff ;
$secondary: #228be6;
$white: #fff;
$gray: #9b9b9b;
.addExpenseParagraph{
 width: 100%;
 &:first-child{
  margin-top: 2rem;

 }
}
.expense-input{
    width: 40%;
    display: inline-block;
    margin: 2rem 0 0 2rem;
  }
  .cost-input{
    width: 10%;
    display: inline-block;
    margin: 2rem 0 0 2rem;
  }
.form__group {
  padding: 15px 0 0;
  margin-top: 10px;
  width: 100%;
}

.form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid $gray;
  outline: 0;
  font-size: 1.3rem;
  color: $gray;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;

  &::placeholder {
    color: transparent;
  }

  &:placeholder-shown ~ .form__label {
    font-size: 1.3rem;
    cursor: text;
    top: 20px;
  }
}

.form__label {
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: $gray;
}

.form__field:focus {
  ~ .form__label {
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: $primary;
    font-weight:700;    
  }
  padding-bottom: 6px;  
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, $primary,$secondary);
  border-image-slice: 1;
}
.form__field{
  &:required,&:invalid { box-shadow:none; }
}

.button{
  position: relative;
  left: 80%;
  margin: 0 1rem 0 1rem;
  border-radius: 0.3rem;
  padding: 0 0.6rem 0 0.6rem;
  font-size: 0.8rem;
}

.checkboxes{
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    max-height: 7rem;
  overflow-y: scroll;
  scrollbar-width: thin;
}
