.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #4dabf7;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;

}

.header-image{
    width: 100%;
    height: 20rem;
    z-index: 0;
    overflow: hidden;
}

.header-image img {
    width: 110%;
    height: 100%;
    object-fit:cover;
    transform: rotateZ(-2deg) translateY(-2rem) translateX(-1rem);
}